const pathsMap = {
  home: () => process.env.PUBLIC_URL +'/',
  main: () => process.env.PUBLIC_URL + '/main/',
  dashboard: () => process.env.PUBLIC_URL +'/main/dashboard',
  setting: () => process.env.PUBLIC_URL +'/main/setting',
};
type PathsMap = typeof pathsMap;

export const getPath = <TRoute extends keyof PathsMap>(
  route: TRoute,
  ...params: Parameters<PathsMap[TRoute]>
) => {
  const pathCb: (...args: any[]) => string = pathsMap[route];

  return pathCb(...params);
};
